
body {
  margin: 0;
  background-color: #0e0e0e;
}
figure {
  margin: 0;
}
.container {
  margin: 2rem auto;
  text-align: center;
}
.artist {
  color: #727272;
  text-align: center;
}
.footer {
  background: #0d0d0d;
  position: fixed;
  bottom: 0;
}
.artist-image {
  border-radius: 100%;
  width: 267px;
  height: 267px;
}
.table {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  margin: 2rem;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.white {
  color: #fff;
}
.error {
  color: #ab0d0d;
  font-size: 3rem;
}
#app {
  background-color: #0d0d0d;
  font-family: 'Montserrat';
}


